body {
  margin: 0;
  font-family: "roboto", Arial, Helvetica, sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d5d4a9;
  overflow-x: hidden;
  width: 100%;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  width: 100vw;
}



.title {
  font-size: 22px;
  font-weight: 700;
  margin-top: 50px;
  text-align: center;
  -webkit-animation: slide 10s both infinite linear;
          animation: slide 10s both infinite linear;
}



@-webkit-keyframes slide{
    from {-webkit-transform: translateX(-100%);transform: translateX(-100%)}
    to {-webkit-transform: translateX(200%);transform: translateX(200%)}

}



@keyframes slide{
    from {-webkit-transform: translateX(-100%);transform: translateX(-100%)}
    to {-webkit-transform: translateX(200%);transform: translateX(200%)}

}

.daily-item {
  background-color: #f5f5f5;
  display: flex;
  align-items: center;
  margin: 5px;
  height: 40px;
  border-radius: 15px;
  padding: 5px 20px;
  font-size: 14px;
  cursor: pointer;
}

.icon-small {
  width: 40px;
}

.day {
  color: #212121;
  flex: 1 1;
  font-weight: 600;
  margin-left: 15px;
}

.description {
  margin-right: 15px;
  text-align: right;
}

.min-max {
  color: #757575;
}

.daily-details-grid {
  display: grid;
  flex: 1 1;
  grid-column-gap: 35px;
  -webkit-column-gap: 35px;
          column-gap: 35px;
  grid-row-gap: 0;
  row-gap: 0;
  grid-template-columns: auto auto;
  padding: 5px 25px;
}

.daily-details-grid-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.daily-details-grid-item label:first-child {
  color: #757575;
}

.daily-details-grid-item label:last-child {
  color: #212121;
}

